<template>
  <button
    class="h-12 w-12 border-none bg-primary text-darker fixed rounded-full text-xl"
    v-show="show"
    v-scroll-to="'#hero'"
  >
    <svg
      role="img"
      xmlns="http://www.w3.org/2000/svg"
      viewBox="0 0 448 512"
      class="w-5 h-5 mx-auto"
    >
      <path
        fill="currentColor"
        d="M6.101 261.899L25.9 281.698c4.686 4.686 12.284 4.686 16.971 0L198 126.568V468c0 6.627 5.373 12 12 12h28c6.627 0 12-5.373 12-12V126.568l155.13 155.13c4.686 4.686 12.284 4.686 16.971 0l19.799-19.799c4.686-4.686 4.686-12.284 0-16.971L232.485 35.515c-4.686-4.686-12.284-4.686-16.971 0L6.101 244.929c-4.687 4.686-4.687 12.284 0 16.97z"
      />
    </svg>
  </button>
</template>

<script>
export default {
  data() {
    return {
      show: false
    };
  },
  methods: {
    showHideBackToTopButton() {
      const pxTrigger = window.innerHeight;

      document.addEventListener("scroll", () => {
        const pxFromTop = window.scrollY || window.pageYOffset;

        this.show = pxFromTop > pxTrigger;
      });
    }
  },
  mounted() {
    this.showHideBackToTopButton();
  }
};
</script>

<style lang="scss" scoped>
button {
  bottom: 10px;
  right: 10px;
  transition: all 0.3 ease !important;
  z-index: 9999;
}
</style>