<template>
  <div id="app" class="bg-darker text-white relative">
    <vue-progress-bar />

    <!-- <Preloader v-if="!loaded" /> -->

    <TheNavigation />

    <div class="container md:pl-64">
      <router-view />
    </div>

    <BackToTop />

    <TheFooter />
  </div>
</template>

<script>
import TheNavigation from "@/components/partials/TheNavigation";
import Preloader from "@/components/preloads/Name";
import TheFooter from "@/components/partials/TheFooter";
import BackToTop from "@/components/partials/BackToTop";

export default {
  data() {
    return {
      loaded: false
    };
  },
  mounted() {
    this.$Progress.finish();
    this.loaded = true;
  },
  created() {
    this.$Progress.start();
    this.$router.beforeEach((to, from, next) => {
      if (to.meta.progress !== undefined) {
        let meta = to.meta.progress;
        this.$Progress.parseMeta(meta);
      }
      this.$Progress.start();
      next();
    });
    this.$router.afterEach((to, from) => {
      this.$Progress.finish();
    });
  },
  components: {
    TheNavigation,
    Preloader,
    TheFooter,
    BackToTop
  }
};
</script>

